import { MultiselectTag } from './MultiselectTag';
export default {
  name: 'FileSystemTag',
  components: {
    MultiselectTag
  },
  props: {
    value: {
      type: String,
      default: ''
    }
  },

  data() {
    return {
      newValue: []
    };
  },

  watch: {
    value() {
      this.updateValue();
    }

  },

  beforeMount() {
    this.updateValue();
  },

  methods: {
    updateValue() {
      if (this.value !== '') {
        this.newValue = this.value;
      } else {
        this.newValue = null;
      }
    },

    update() {
      this.$emit('input', this.newValue || '');
    }

  }
};