import Multiselect from '@nextcloud/vue/dist/Components/Multiselect';
import { searchTags } from './api';
let uuid = 0;
export default {
  name: 'MultiselectTag',
  components: {
    Multiselect
  },
  props: {
    label: {
      type: String,
      required: true
    },
    value: {
      type: [String, Array],
      default: null
    },
    disabled: {
      type: Boolean,
      default: false
    },
    multiple: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      inputValObjects: [],
      tags: []
    };
  },

  computed: {
    id() {
      return 'settings-input-text-' + this.uuid;
    }

  },
  watch: {
    value(newVal) {
      this.inputValObjects = this.getValueObject();
    }

  },

  beforeCreate() {
    this.uuid = uuid.toString();
    uuid += 1;
    searchTags().then(result => {
      this.tags = result;
      this.inputValObjects = this.getValueObject();
    }).catch(console.error.bind(this));
  },

  methods: {
    getValueObject() {
      if (this.tags.length === 0) {
        return [];
      }

      if (this.multiple) {
        return this.value.filter(tag => tag !== '').map(id => this.tags.find(tag2 => tag2.id === id));
      } else {
        return this.tags.find(tag => tag.id === this.value);
      }
    },

    update() {
      if (this.multiple) {
        this.$emit('input', this.inputValObjects.map(element => element.id));
      } else {
        if (this.inputValObjects === null) {
          this.$emit('input', '');
        } else {
          this.$emit('input', this.inputValObjects.id);
        }
      }
    },

    tagLabel(_ref) {
      let {
        displayName,
        userVisible,
        userAssignable
      } = _ref;

      if (userVisible === false) {
        return t('systemtags', '%s (invisible)').replace('%s', displayName);
      }

      if (userAssignable === false) {
        return t('systemtags', '%s (restricted)').replace('%s', displayName);
      }

      return displayName;
    }

  }
};