import Multiselect from '@nextcloud/vue/dist/Components/Multiselect';
import axios from '@nextcloud/axios';
import { generateOcsUrl } from '@nextcloud/router';
const groups = [];
const status = {
  isLoading: false
};
export default {
  name: 'RequestUserGroup',
  components: {
    Multiselect
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    check: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },

  data() {
    return {
      groups,
      status
    };
  },

  computed: {
    currentValue() {
      return this.groups.find(group => group.id === this.value) || null;
    }

  },

  async mounted() {
    if (this.groups.length === 0) {
      await this.searchAsync('');
    }

    if (this.currentValue === null) {
      await this.searchAsync(this.value);
    }
  },

  methods: {
    searchAsync(searchQuery) {
      if (this.status.isLoading) {
        return;
      }

      this.status.isLoading = true;
      return axios.get(generateOcsUrl('cloud/groups/details?limit=20&search={searchQuery}', {
        searchQuery
      })).then(response => {
        response.data.ocs.data.groups.forEach(group => {
          this.addGroup({
            id: group.id,
            displayname: group.displayname
          });
        });
        this.status.isLoading = false;
      }, error => {
        console.error('Error while loading group list', error.response);
      });
    },

    addGroup(group) {
      const index = this.groups.findIndex(item => item.id === group.id);

      if (index === -1) {
        this.groups.push(group);
      }
    }

  }
};