import Multiselect from '@nextcloud/vue/dist/Components/Multiselect';
import moment from 'moment-timezone';
import valueMixin from '../../mixins/valueMixin';
const zones = moment.tz.names();
export default {
  name: 'RequestTime',
  components: {
    Multiselect
  },
  mixins: [valueMixin],
  props: {
    value: {
      type: String,
      default: ''
    }
  },

  data() {
    return {
      timezones: zones,
      valid: false,
      newValue: {
        startTime: null,
        endTime: null,
        timezone: moment.tz.guess()
      }
    };
  },

  mounted() {
    this.validate();
  },

  methods: {
    updateInternalValue(value) {
      try {
        const data = JSON.parse(value);

        if (data.length === 2) {
          this.newValue = {
            startTime: data[0].split(' ', 2)[0],
            endTime: data[1].split(' ', 2)[0],
            timezone: data[0].split(' ', 2)[1]
          };
        }
      } catch (e) {// ignore invalid values
      }
    },

    validate() {
      this.valid = this.newValue.startTime && this.newValue.startTime.match(/^(0[0-9]|1[0-9]|2[0-3]|[0-9]):[0-5][0-9]$/i) !== null && this.newValue.endTime && this.newValue.endTime.match(/^(0[0-9]|1[0-9]|2[0-3]|[0-9]):[0-5][0-9]$/i) !== null && moment.tz.zone(this.newValue.timezone) !== null;

      if (this.valid) {
        this.$emit('valid');
      } else {
        this.$emit('invalid');
      }

      return this.valid;
    },

    update() {
      if (this.newValue.timezone === null) {
        this.newValue.timezone = moment.tz.guess();
      }

      if (this.validate()) {
        const output = "[\"".concat(this.newValue.startTime, " ").concat(this.newValue.timezone, "\",\"").concat(this.newValue.endTime, " ").concat(this.newValue.timezone, "\"]");
        this.$emit('input', output);
      }
    }

  }
};